@charset "UTF-8";
/* リキッドレイアウト対応 */
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700&display=swap");
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 2vw;
  }
}
@media (min-width: 800px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.header__drawer {
  z-index: 9999;
  background: #024892;
  padding: 0 1.3125rem;
}
@media screen and (min-width: 768px) {
  .header__drawer {
    display: none;
  }
}

.header__drawer span {
  position: relative;
  display: block;
  height: 2px;
  width: 28px;
  border-radius: 0.625rem;
  background: #fff;
  transition: ease 0.3s;
}

.header__drawer span:nth-child(1) {
  top: 0;
}

.header__drawer span:nth-child(2) {
  margin: 0.625rem 0;
}

.header__drawer span:nth-child(3) {
  top: 0;
}

/*is-OPEN時の動き*/
.header__drawer.is-open span:nth-child(1) {
  top: 0.625rem;
  transform: rotate(45deg);
}

.header__drawer.is-open span:nth-child(2) {
  transform: translateY(-50%);
  opacity: 0;
}

.header__drawer.is-open span:nth-child(3) {
  top: -0.25rem;
  transform: rotate(-45deg);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 4.375rem;
  z-index: 99999;
  background-color: #fff;
  color: #111111;
}

.header__inner {
  display: flex;
  height: inherit;
  justify-content: space-between;
}
@media screen and (min-width: 768px) {
  .header__inner {
    padding: 0 0 0 1.875rem;
  }
}

.header__logo {
  width: 13.4375rem;
}
@media screen and (min-width: 768px) {
  .header__logo {
    width: 13.4375rem;
    height: 3.75rem;
    padding-top: 0.3125rem;
  }
}

.header__sp-nav {
  display: none;
  position: absolute;
  background-color: #000;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  z-index: 8888;
}

.header__right {
  display: none;
}
@media screen and (min-width: 768px) {
  .header__right {
    display: flex;
    gap: 1.25rem;
  }
}

.header__pc-nav {
  display: none;
}
@media screen and (min-width: 768px) {
  .header__pc-nav {
    display: block;
    display: flex;
    align-items: center;
  }
}

.inner {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 37.5rem;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 850px;
    padding-right: 25px;
    padding-left: 25px;
    width: 100%;
  }
}

.pc-nav__items {
  display: flex;
  align-items: center;
  height: inherit;
  justify-content: right;
}

.pc-nav__item {
  height: inherit;
}

.pc-nav__item a {
  position: relative;
  color: #111111;
  font-size: 0.875rem;
  height: inherit;
  display: grid;
  place-items: center;
  padding: 0 0.78125rem;
  letter-spacing: 0.125em;
}

.pc-nav__item a::after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 0.0625rem;
  background: #FFEA2E;
  bottom: 0.5rem;
  visibility: hidden;
}

.pc-nav__item a:hover::after {
  visibility: visible;
}

.pc-nav__item.pc-nav__item--white {
  margin-left: 0.9375rem;
}
.pc-nav__item.pc-nav__item--white :hover {
  background-color: #333;
  color: #fff;
}

.pc-nav__item.pc-nav__item--white a::after {
  display: none;
}

.pc-nav__item.pc-nav__item--white a {
  background-color: #fff;
  color: #333;
  padding-left: 2rem;
  padding-right: 2.1875rem;
}
.pc-nav__item.pc-nav__item--white a :hover {
  color: #fff;
}

.pc-nav__tel {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  justify-content: center;
  padding-left: 0.78125rem;
}

.pc-nav__telimg {
  width: 1.0625rem;
  height: auto;
}

.pc-nav__telimg img {
  width: 1.0625rem;
  height: auto;
}

.pc-nav__teldetails {
  display: flex;
  flex-direction: column;
}

.pc-nav__telnumber {
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 700;
}

.pc-nav__teltime {
  display: inline-block;
  font-size: 0.625rem;
  font-weight: 500;
}

.pc-nav__contact {
  display: none;
}
@media screen and (min-width: 768px) {
  .pc-nav__contact {
    display: block;
    color: #fff;
    background-color: #024892;
    padding: 1.375rem 4.0625rem 1.1875rem 4.0625rem;
  }
}

.sp-nav__items {
  margin-left: 0.125rem;
  padding-top: 12rem;
  padding-bottom: 13rem;
  height: 100vh;
  overflow: scroll;
}

.sp-nav__item a {
  display: block;
  width: 100%;
  padding-top: 1rem;
  padding-bottom: 0.9375rem;
  text-align: center;
  color: #fff;
  font-size: 1rem;
  letter-spacing: 0.125em;
}

.contact {
  margin-top: 3.125rem;
}
@media screen and (min-width: 768px) {
  .contact {
    margin-top: 3.75rem;
  }
}

.contact__wrap {
