@media screen and (min-width: 768px) {
  .form__item {
    display: grid;
    grid-template-columns: 9.5rem 1fr;
    align-items: center;
    gap: 1.75rem;
  }
}

@media screen and (min-width: 768px) {
  .form__item--textarea {
    align-items: start;
