.form-radio {
  margin-top: 0.625rem;
}
@media screen and (min-width: 768px) {
  .form-radio {
    margin: 0.375rem 0;
  }
}

.form-radio__item {
  display: inline-block;
}

.wpcf7-list-item + .wpcf7-list-item {
  margin-left: 1.5625rem;
}

.wpcf7-radio .wpcf7-list-item-label {
  cursor: pointer;
  display: inline-block;
  position: relative;
  color: #111111;
  font-size: 1rem;
  font-weight: 400;
  padding-left: 0.8125rem;
}
@media screen and (min-width: 768px) {
  .wpcf7-radio .wpcf7-list-item-label {
    font-size: 1rem;
  }
}

.wpcf7-radio .wpcf7-list-item-label:before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 50%;
  border: 2px solid #024892;
  width: 1rem;
  height: 1rem;
  left: -0.5rem;
  top: 50%;
  transform: translateY(-50%);
}

.wpcf7-radio .wpcf7-list-item-label:after {
  content: "";
  display: block;
  position: absolute;
  left: -0.3125rem;
  border-radius: 50%;
  width: 0.625rem;
  height: 0.625rem;
  background-color: #024892;
  top: 0.4375rem;
  opacity: 0;
}

input[type=radio]:checked + .wpcf7-list-item-label:after {
  opacity: 1;
}

.form__textarea {
  font-size: 1rem;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  background-color: #f5f5f5;
  width: 100%;
  height: 12.625rem;
  padding: 0.6875rem 0.625rem;
}
@media screen and (min-width: 768px) {
