.form__consent {
  margin-top: 1.875rem;
  font-size: 0.875rem;
  color: #111111;
  font-weight: 400;
  letter-spacing: -0.02em;
}
@media screen and (min-width: 768px) {
  .form__consent {
    text-align: center;
    margin-top: 0.5rem;
    font-size: 0.875rem;
