}

.form__submit-wrap {
  text-align: center;
  margin-top: 1.5rem;
  position: relative;
}

.form__submit-wrap::after {
  content: "";
  position: absolute;
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  left: 70%;
  top: 50%;
  transform: translate(-70%, -50%) rotate(45deg);
  border-top: 1px solid #fff;
  border-right: 1px solid #fff;
}
@media screen and (min-width: 768px) {
  .form__submit-wrap::after {
    left: 60%;
